@font-face {
  font-family: 'icomoon';
  src:  url('fonts/icomoon.eot?70t8lt');
  src:  url('fonts/icomoon.eot?70t8lt#iefix') format('embedded-opentype'),
    url('fonts/icomoon.ttf?70t8lt') format('truetype'),
    url('fonts/icomoon.woff?70t8lt') format('woff'),
    url('fonts/icomoon.svg?70t8lt#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

